<template>
    <div>
      <heer :obj="self"></heer>  
      <div style="width:60%;margin:0 auto;padding:60px 0">
          <p style="width:100%;height:34px;border-radius:17px;background:#f23232;line-height:34px;text-align:center;color:#fff;font-weight:700;font-size:18px;">IELTS</p>
          <div style="padding:30px 0 40px">
            <p v-for="i in on" style="color:#696969;font-size:14px;line-height:34px">
                {{i}}
            </p>
          </div>
          <p style="width:100%;height:34px;border-radius:17px;background:#f23232;line-height:34px;text-align:center;color:#fff;font-weight:700;font-size:18px;"></p>
          <div style="padding:40px 0 20px;border-bottom:1px solid #ccc">
              <div v-for="i in kcname" style="color:#545454;font-size:14px;line-height:28px;margin-bottom:30px">
                  <p><strong>{{i.name}}</strong>:{{i.con}}</p>
                  <strong>{{i.td}}</strong>:{{i.tdcon}}
              </div>
          </div>
      </div>
    </div>
</template>


<script>
import bus from "../component/bus.vue"
import heer from "./header.vue"
export default {
    components:{
        heer,
    }, 
    data() {
        return {
            selfindex:3,
             self:{
                title:"IELTS ",
                en:"Colleges And Universities Recommend",
                type:false
            },
            on:[
                '',
                '',
                '',
                '',
                '',
            ],
            kcname:[
                {
                    name:"#01",
                    con:"  ",
                    td:"",
                    tdcon:"  "
                },
               {
                    name:"#02",
                    con:"  ",
                    td:"",
                    tdcon:"  "
                },
            ]
        }
    },  
    mounted() {
        bus.$emit("lee",this.selfindex)
    },
}
</script>

<style>
    
</style>


