<template>
    <div>
        <heer :obj="self"></heer>
        <div style="width:60%;margin:0 auto;padding-top:50px;padding-bottom:100px;">
            <div style="color:#ff6666;font-size:28px;font-weight:700;text-align:center;border-bottom:3px solid rgb(247, 197, 197);padding-bottom:6px">
                
                
            </div>
            <div v-for="(i,$index) in quetion" style="border-bottom:1px solid #ccc;padding-bottom:20px" class="zlzs-que">
                <p class="zlzs-p" :style="{color:index==$index?'#f96b6b':'#f9acac'}" @click="btn($index)">{{i.title}}<span :class="index!=$index?'fr glyphicon glyphicon-plus zlzs-j':'fr glyphicon glyphicon-minus zlzs-j'" :style="{background:index==$index?'rgb(249, 107, 107)':'rgb(249, 172, 172)'}"></span></p>
                <div :style="{height:index==$index?'600px':'0px'}" class="zlzs-ok">
                    <p style="padding:40px ;color:rgb(128, 128, 128);line-height:40px;letter-spacing:1pxtransition:.35s;">
                        {{i.con}}
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import bus from "../component/bus.vue"
import heer from "./header.vue"
export default {
    data() {
        return {
            selfindex:3,
            index:-1,
            self:{
                title:"",
                en:"Colleges And Universities Recommend"
            },
        quetion:[
            {
                title:"",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"CO-OP",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"AACSB",
                con:"    16    1  2()  3      4036001200"
            },
            {
                title:"",
                con:"    16    1  2()  3      4036001200"
            },
        ]
        }
    },
    mounted() {
        bus.$emit("lee",this.selfindex)
    },
       components:{
        heer,
    },   
    methods: {
        btn(i){
           
            if(this.index==i){
                this.index=-1
            }else{
                 this.index=i
            }
        }
    },
    
    
}
</script>

<style>
.zlzs-que:last-child{
    border: none !important
}
.zlzs-ok{
    overflow:hidden;
    height:0px;
    transition:.35s;
}
    .zlzs-j{
        text-align: center;
        line-height: 30px;
        color:#fff;
        width:26px;
        height:30px;
        /* padding:3px 5px; */
        background:rgb(249, 172, 172);

    }
    .zlzs-p{
        color:#f9acac;
        line-height:28px;
        padding:20px 0 0;
    }
</style>


